* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-decoration: none;
}

.app {
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: max-content 1fr;
      grid-template-rows: -webkit-max-content 1fr;
      grid-template-rows: max-content 1fr;
  height: 100vh;
}

.header {
  padding-top: 1rem;
  background-color: white;
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: max-content max-content;
      grid-template-rows: -webkit-max-content -webkit-max-content;
      grid-template-rows: max-content max-content;
}

.logo-wewine {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 2rem;
  overflow: hidden;
}

.logo-wewine__bottle .--svgBottle {
  height: 4rem;
}

.logo-wewine__bottle .--svgBottle .cls-1 {
  fill: #D0B0AF;
}

.logo-wewine__bottle .--svgBottle .cls-2 {
  fill: #9B4843;
}

.logo-wewine__bottle .--svgBottle .cls-3 {
  fill: #632624;
}

.logo-wewine__words .--svgWords {
  height: 4rem;
  fill: #632624;
}

.nav {
  position: relative;
  width: 80%;
  -ms-grid-column-align: center;
      justify-self: center;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.nav__logo {
  position: absolute;
  top: 50%;
  left: 0;
  opacity: 0;
  -webkit-transform: translate(-145%, -50%);
          transform: translate(-145%, -50%);
}

.nav__logo .--svgBottle {
  height: 3rem;
}

.nav__logo .--svgBottle .cls-1 {
  fill: #D0B0AF;
}

.nav__logo .--svgBottle .cls-2 {
  fill: #9B4843;
}

.nav__logo .--svgBottle .cls-3 {
  fill: #632624;
}

.nav__underline {
  position: absolute;
  content: "";
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 2px;
  width: 100%;
  background-color: #9B4843;
}

.tab {
  position: relative;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.tab__icon .--svg {
  height: 3em;
  stroke: #9B4843;
  fill: #9B4843;
  -webkit-transition: 300ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: 300ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -webkit-transition-property: stroke, fill, stroke-width, -webkit-transform;
  transition-property: stroke, fill, stroke-width, -webkit-transform;
  transition-property: stroke, fill, stroke-width, transform;
  transition-property: stroke, fill, stroke-width, transform, -webkit-transform;
}

.tab__name {
  color: #9B4843;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  padding-bottom: 0.5em;
  -webkit-transition: 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -webkit-transition-property: font-weight, color, -webkit-transform;
  transition-property: font-weight, color, -webkit-transform;
  transition-property: transform, font-weight, color;
  transition-property: transform, font-weight, color, -webkit-transform;
}

.tab::after {
  position: absolute;
  content: '';
  z-index: 1;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 50%;
  height: 0;
  border-radius: 1em 1em 0 0;
  background-color: #9B4843;
  -webkit-transition: 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -webkit-transition-property: background-color, height, width;
  transition-property: background-color, height, width;
}

.tab:hover .tab__icon .--svg {
  -webkit-transform: translateY(-10px) scale(0.95);
          transform: translateY(-10px) scale(0.95);
}

.tab:hover .tab__name {
  -webkit-transform: translateY(-6px);
          transform: translateY(-6px);
}

.tab:hover::after {
  height: 6px;
}

.--currentTab {
  pointer-events: none;
}

.--currentTab .tab__icon .--svg {
  stroke: #632624;
  stroke-width: .5rem;
  fill: #632624;
}

.--currentTab .tab__name {
  color: #632624;
  font-weight: 900;
}

.--currentTab .tab::after {
  height: 5px;
  width: 60%;
  background-color: #632624;
}

.content {
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  padding-top: 2rem;
}

.content.--not-scrollable {
  overflow: hidden;
}

.content h1 {
  margin-bottom: 2rem;
}

.content p {
  letter-spacing: 1px;
  line-height: 1.75rem;
}

.content section {
  padding: 4rem;
}

.search-tab {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #9B4843;
  color: white;
  padding: 4rem;
}

.account-tab {
  position: absolute;
}

.cave-tab {
  position: absolute;
}

.home-tab {
  position: absolute;
}

.shop-tab {
  position: absolute;
}
